import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { getPointLadderFours, deletePointLadderFourById } from 'services/pointLadderFour';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__DIEM_XEP_LOAI__XL_HOC_TAP_THANG_4__NHAP_DU_LIEU;

class PointLadderFourPage extends Component {
  state = { reload: 0 };

  columns = [
    {
      title: 'Xp loi',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'Tn ting anh',
      dataIndex: 'labelInEnglish',
      key: 'labelInEnglish',
    },
    {
      title: 'T im',
      dataIndex: 'fromGrade',
      key: 'fromGrade',
    },
    {
      title: 'n im',
      dataIndex: 'toGrade',
      key: 'toGrade',
    },
    {
      title: 'Thao tc',
      dataIndex: 'operation',
      render: (text, record) => (
        <Fragment>
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        </Fragment>
      ),
    },
  ];

  handleDelete = async id => {
    try {
      await deletePointLadderFourById(id);
      message.success('Xa thnh cng');
      this.setState({ reload: (this.state.reload += 1) });
    } catch (err) {
      message.error('Xa khng thnh cng');
    }
  };

  render() {
    const { reload } = this.state;

    return (
      <Card title="Xp loi theo thang im 4">
        <Row>
          <Col span={24}>
            <Link to={ROUTER_FORM_DATA}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              bordered
              columns={this.columns}
              service={getPointLadderFours}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <PointLadderFourPage />
  </Page>
);



